import axios from "axios";

export const getApi = (url, conf = {}) => {
  return axios
    .get(url, conf)
    .then((response) => {
      return response?.data;
    })
    .catch((error) => {
      throw error;
    });
};

export const postApi = (url, data = {}, conf = {}) => {
  return axios
    .post(url, data, conf)
    .then((response) => {
      return response?.data;
    })
    .catch((error) => {
      throw error;
    });
};

export const updateApi = (url, data = {}, conf = {}) => {
  return axios
    .patch(url, data, conf)
    .then((response) => {
      return response?.data;
    })
    .catch((error) => {
      throw error;
    });
};

export const deleteApi = (url, conf = {}) => {
  return axios
    .delete(url, conf)
    .then((response) => {
      return response?.data;
    })
    .catch((error) => {
      throw error;
    });
};
